<template>
<div style=" margin: 0 auto;width:98%;" >
    <div class="className" id="multipleBarsLines2" style="height:320px;width:98%; "></div>
</div>
</template>

<script>
import echarts from "echarts";
export default {
    props: ["pointLists"],
    props:{
        pointLists:{
         type:Object,
         default(){
            return {}
         }
     },
    },
    watch: {
        // pointLists: function (newVal, oldVal) {
        //     this.title = newVal.title
        //     this.actual = newVal.analysis.value;
        //     this.target = newVal.target.value;
        //     this.indevaval = newVal.indevaval.value
        //     this.courseNames = newVal.value1;
        //     this.initChart2();
        // }
    },
    data() {
        return {
            chart: null,
            actual: [],
            target: [],
            courseNames: [],
            title:'',
            indevaval:[]
        };
    },
    created() {
        setTimeout(() =>{
            // if(this.pointLists.title){
            //     this.indevaval = this.pointLists.indevaval.value
            //     this.title = this.pointLists.title
            //     this.actual = this.pointLists.analysis.value;
            //     this.target = this.pointLists.target.value;
            //     this.courseNames = this.pointLists.value1;
                this.initChart2();
            // }
        },500);
    },
    methods: {
        initChart2() {
            this.chart = echarts.init(document.getElementById("multipleBarsLines2"));
            var option = {
                tooltip: {
                    position: function (point) {
                        return [point[0], "15%"];
                    },
                    // formatter: function(p) {
                    //   var index = p.dataIndex;
                    //   return `${p.name} <br/> ${p.seriesName} : ${p.value}<br/>`;
                    // }
                },
                //   小图标   折线  转换柱状图
                toolbox: {
                    show: true,
                    top: -5,
                    feature: {
                        saveAsImage: {
                            show: true,
                        },

                    }
                },
                title: {
                    // text: this.title,
                    text: '各学院学生达成率排行',
                    textStyle: {
                      color: '#333',
                      fontSize: 20,
                    }
                },
                legend: {
                    // data:["机械学院", "土木学院", "经济学院", "管理学院","机械学院", "机械学院", "机械学院", "机械学院",],
                    right: 21,
                    top: 0,
                    textStyle: {
                        fontSize: 16,
                        color: "#ccc"
                    }
                },
                grid: {
                    left: "3%",
                    right: "4%",
                    bottom: "10%",
                    containLabel: true
                },
                xAxis: {
                    axisLabel: {
                        fontSize: 14,
                        color: "rgba(3,177,135,1)"
                    },
                    axisLine: {
                        show: false,
                        lineStyle: {
                            color: "#aaa",
                            show: false
                        }
                    },
                    axisTick: {
                        show: false,
                        color: '#323232'
                    },
                    // data: this.courseNames,
                    data:["机械学院", "土木学院", "经济学院", "管理学院","机械学院1", "机械学院2", "机械学院3", "机械学院4",],
                    axisLabel: {
                        //坐标轴刻度标签的相关设置。this.courseNames,
                        interval: 0,
                        rotate: this.courseNames.length > 10 ? 45 : 0 //代表逆时针旋转45度
                    }
                },
                dataZoom: [{
                    type: "slider",
                    show: true,
                    xAxisIndex: [0],
                    left: "3%",
                    bottom: -25,
                    start: 0,
                    end: 90 //初始化滚动条
                }],
                yAxis: {
                    type: 'value',
                    axisLine: {
                        show: false,
                        lineStyle: {
                            color: "#969696"
                        }
                    },
                    splitLine: {
                        show: true,
                        lineStyle: {
                            color: '#F4F4F4'
                        }
                    },
                    axisTick: {
                        show: false,
                        color: '#323232'
                    },
                },
                series: [{
                        // name: "机械学院",
                        // name:["机械学院", "土木学院", "经济学院", "管理学院","机械学院1", "机械学院2", "机械学院3", "机械学院4",],
                        type: "bar",
                        barGap: "0%",
                        barCategoryGap: "80%",
                        itemStyle: {
                            barBorderRadius: [5,5,0,0],
                            normal:{  
                　　　　　　　　　　　　//每个柱子的颜色即为colorList数组里的每一项，如果柱子数目多于colorList的长度，则柱子颜色循环使用该数组
                                color: function (params){
                                    var colorList = ['#00CB9A','#3D8FFF','#C88CFF','#FFE098','#5FD4FF','#784EFF','#FFA351','#43DABC'];
                                    return colorList[params.dataIndex];
                                }
                            },
                        },
                        barWidth:30,
                        label: {
                            show: false
                        },
                        // data: this.actual
                        data:[30,15,14,26,29,30,36,40]
                    },
                ]
            };
            this.chart.setOption(option);
        }
    }
};
</script>

<style lang="scss">
// .className{
//     width:100%;
//     height: 500px;
// }
</style>



