<template>
    <div class="echarts_pp">
        <div :id="id" :style="{height:chartHeight,width:chartWidth}" class="scatter"></div>
        <p class="echarts_p">{{title}}</p>
    </div>
</template>

<script>
import echarts from 'echarts'
export default {
 name: 'PieCircle',
 props:{
     id:{
         type:String,
         default:'pieCircle'
     },
     chartHeight:{
         type:String,
         default:'400px'
     },
     chartWidth:{
         type:String,
         default:'100%'
     },
     pieCircleData:{
         type:Array,
         default(){
             return []
         }
     },
      color:{
         type:Array,
         default(){
             return ['#00CB9A','#3D8FFF','#C88CFF','#FFE098','#5FD4FF','#784EFF','#FFA351','#43DABC']
         }
     },
 },
 data() {
    return {
        chart:null,
        title:''

    }
 },
 created(){},
 mounted(){
     this.$nextTick(()=>{
         this.initChart(); 
     })
    
    
 },
  watch:{
     pieCircleData(curVal,oldVal){
         this.initChart(); 
     },
 },
 methods:{
      initChart(){
        const self=this;
        self.chart = echarts.init(document.getElementById(self.id));
        self.title = self.title
        self.chart.setOption(self.getSeriesBarOption());
        window.addEventListener('resize', function(){
            if(self.chart){
                self.chart.resize()
            }
            
        })
     },
     getSeriesBarOption(){
          const self=this
          if(self.pieCircleData){
              self.title = self.pieCircleData[0].title
          }
          
          return {
            tooltip: {
                trigger: 'item',
                formatter: "{a} {b} : {c} ({d}%)"
            },
            color:self.color,
            toolbox: {
                show: true,
                top: -5,
                feature: {
                    saveAsImage: {
                        show: true,
                    },
                }
            },
            title: { // 饼图的title 和中间显示的样式 字体颜色
                // text: this.sumNumber,
                text:self.pieCircleData[0].text,
                subtext:self.pieCircleData[0].subtext,
                textAlign:"center",
                x: '28%',
                y: '48%',
                textStyle: {
                    color: "#03B187",
                    fontSize: 40,
                    fontWeight: "normal",
                    align: "center",
                    // width: "200px"
                },
                subtextStyle: {
                    color: "#323232",
                    fontSize: 14,
                    fontWeight: "normal",
                    align: "center"
                }
            },
            legend: {  // 图例的配置属性
                orient: 'vertical',
                left:'58%',
                align:'left',
                top:'48%',
                textStyle: {
                    color:'#8C8C8C'
                },
                height:120, //图例的高度
                itemGap:20 //图例之间的距离
            },
            series: [
                {
                    name:'',
                    type:'pie',
                    radius: ['50%', '70%'],
                    hoverAnimation: false,
                    avoidLabelOverlap: false,
                    type: "pie",
                    center: ["28%", "58%"],
                    // radius: ['36%', '50%'],
                    itemStyle: {
                        normal: {
                            label: {
                            position: 'center',
                            show: false,
                            textStyle: {
                                fontSize: 12
                            },
                            formatter: function(params) {
                                return "{a|" + params.data.name + ":}" +
                                    "{b|" + params.data.value+"个}";
                            },
                            },
                            labelLine: {
                                show: true
                            }
                        },
                        emphasis: {
                            shadowBlur: 10,
                            shadowOffsetX: 0,
                            shadowColor: "rgba(0, 0, 0, 0.5)"
                        }
                        },
                    data:self.pieCircleData 
                }
            ]
          }
     }
  },
  beforeDestroy() {
      if (!this.chart) {
        return
      }
      this.chart.dispose();
      this.chart = null;
    }
}
</script>

<style lang='scss' scoped>
.echarts_pp{
    position: relative;
    .scatter{
        background-color: #fff;
        margin-top: 15px;
        
    }
    .echarts_p{
        position: absolute;
        top: 29px;
        left: 45px;
        font-size: 20px;
        color: #323232;
        font-weight: 700;
    }
}

    
</style>



