<template>
    <div style="border-radius:10px;width:94%;margin: 30px 1.5vw 30px 30px;float: left;position: relative;">
        <div class="className" id="Brokenid" style="height:360px;"></div>
        <!-- <p class="echarts_p">课程数量</p> -->
    </div>
     
</template>
<script>
import echarts from "echarts";
export default {
    props:['chartsDataone'],
    watch: {
        // chartsDataone(newVal,oldVal){
           
        //     let sum = 0
        //     let arr = []
        //     newVal.forEach(element => {
        //         let obj = {
        //             name:'',
        //             value:0
        //         }
        //         this.classifyName.push(element.classifyName)
        //         sum  = sum + Number(element.courseCount)
        //         obj.name = element.classifyName
        //         obj.value = element.courseCount
        //         arr.push(obj)
        //     });
        //     this.sumNumber = sum
        //     this.courseCounts = arr
        //     if(newVal){
        //         this.pieCircle();
        //     }
        // }
    },
    data(){
        return{
            classifyName:[],
            sumNumber:'',
            courseCounts:[]
        }
    },
    mounted() {
        // this.$nextTick(() => {
            setTimeout(() => {
               this.Brokenlist();
            }, 400); 
        // });
    },
    methods:{
        Brokenlist(){
            this.chart = echarts.init(document.getElementById("Brokenid"));
            var option = {
                title: {
                    text: '历年能力达成人数趋势'
                },
                legend: {
                    icon:'circle',
                    // orient 设置布局方式，默认水平布局，可选值：'horizontal'（水平） ¦ 'vertical'（垂直）
                    // orient: 'horizontal',
                    // x 设置水平安放位置，默认全图居中，可选值：'center' ¦ 'left' ¦ 'right' ¦ {number}（x坐标，单位px）
                    x: 'center',
                    // y 设置垂直安放位置，默认全图顶端，可选值：'top' ¦ 'bottom' ¦ 'center' ¦ {number}（y坐标，单位px）
                    y: 'bottom',
                    data: ['假设'],
                    textStyle: {
                        fontSize: 16,
                        color: "#ccc"
                    }
                },
                toolbox: {
                    feature: {
                        saveAsImage: {} //下载工具
                    }
                },
                yAxis: {
                    type: 'value',
                    // min:0, // 设置y轴刻度的最小值
                    // max:1800,  // 设置y轴刻度的最大值
                    // splitNumber:9,  // 设置y轴刻度间隔个数
                    axisLine: {
                        show: false,
                        lineStyle: {
                        // 设置y轴颜色
                        
                        }
                    },
                    axisLabel: {
                        formatter: '{value} 人'
                    },
                    splitLine: {
                        show: true,
                        lineStyle: {
                            color: '#e0dcdc'
                        }
                    },
                    axisTick: {
                        show: false,
                        color: '#323232'
                    },
                },
                xAxis: {
                    data: ['2015年', '2016年', '2017年', '2018年'],
                    axisLine: {
                        show: false,
                        lineStyle: {
                            color: "#aaa",
                            show: false
                        }
                    },
                    axisTick: {
                        show: false,
                        color: '#323232'
                    },
                },
                series: [
                    {
                        name: '假设',
                        data: [120, 232, 0, 134, 290, 130, 120],
                        type: 'line',
                        // 设置折线上圆点大小
                        symbolSize:10,
                        // 设置拐点为实心圆
                        symbol:'circle',  
                                
                        itemStyle: {
                        normal: {
                            color: '#03B187',
                            // 拐点上显示数值
                            label : {
                                show: true
                            },
                            lineStyle:{
                                // 使用rgba设置折线透明度为0，可以视觉上隐藏折线
                                color: '#03B187'
                            }
                        }
                        }
                    }
                ]
            };
            this.chart.setOption(option)
        } 
    }
}
</script>
<style lang="scss">
    .echarts_p{
        position: absolute;
        top: 29px;
        left: 45px;
    }
</style>


